.offer__title {
    span {
        margin-top: 100px;
    }
}
.offer__container {
    display: flex;
    flex-direction: column;
    height: 100%;
    overflow: hidden;
}
.offer__bottom {
    display: flex;
    justify-content: center;
    flex-direction: column;    
    width: fit-content;
    margin: 0 auto;
    margin-top: 100px;
    @media (min-width: 768px) {
        flex-direction: row;
        width: 100%;
        align-items: center;
    }
}

.offer__item {
    font-weight: 300;
    font-size: 20px;
    width: fit-content;
    margin-right: 50px;
    margin-bottom: 30px;
    @media (min-width: 768px) {
        margin-bottom: 0;
    }
    p {
        margin-bottom: 5px;
        a {
            text-decoration: none;
            color: $white;
            transition: .4s;
            &:hover {
                color: $gold;
            }
        }
    }
}
.offer__enroll .social {
    margin: 0;
    margin-top: 20px;
    @media (min-width: 768px) {
        margin-top: 40px;
    }
    &:hover {
        color: $gold;
    }
}

.offer__map {
    margin-top: 40px;
    width: 100%;
    height: 400px;
    background: white;
}